.collaborator {
  width: 80%;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  border-radius: 20px;
  overflow: hidden;
  position: relative;
  margin-bottom: 8%;
}
.collaborator__close {
  position: absolute;
  right: 10px;
  top: 10px;
  z-index: 1;
  cursor: pointer;
}
.collaborator__header {
  position: relative;
  height: 70px;
}
.collaborator__img {
  position: absolute;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  top: 20px;
  left: 0;
  right: 0;
  margin: auto;
  z-index: 1;
}
.collaborator__body {
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  height: 170px;
  padding: 5%;
  box-sizing: border-box;
}
.collaborator__name {
  color: #6278f7;
  font-size: 1rem;
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  text-align: center;
}
.collaborator__profession {
  font-size: 0.889rem;
  color: #212121;
  text-align: center;
  padding-bottom: 5%;
}

@media (min-width: 768px) {
  .collaborator {
    margin-bottom: 3%;
    width: 30%;
  }
}
@media (min-width: 1024px) {
  .collaborator {
    width: calc((90% - 3.9rem) / 4);
  }
  .collaborator__header {
    height: 90px;
  }
  .collaborator__img {
    width: 120px;
    height: 120px;
  }
  .collaborator__body {
    height: 160px;
  }
}
